import React, {useContext, useState} from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { Form, FormField } from 'semantic-ui-react';
import { Button } from 'react-bootstrap';
import { authContext } from '../contexts/AuthContext';

const SignInPage = () => {
    const navigate = useNavigate();
    
    //       
    const { setAuthData } = useContext(authContext);

    //         ,         set...
    const [login, setLogin] = useState('');
    const [password, setPassword] = useState('');
    
    //       
    //  ,     false
    const [isDataCorrect, setIsDataCorrect] = useState('default');

    function onFormSubmit(e) {
        e.preventDefault(); //    
        //,        localStorage   
        if (localStorage.getItem(login) === password) {
            //     
            setLogin('');
            setPassword('');
            //    
            setAuthData(login) ;
            //    
            navigate("/content", { replace: true });
        } else {
            //    false
            setIsDataCorrect(false) ;
        };
    };

    return (
        <section className="signIn">
            <div className='container'>
                <div className="signIn__wrapper">
                    <p className="title"></p>
                    <Form onSubmit={onFormSubmit}>
                        <Form.Group>
                            
                            <FormField className='signIn__field'>
                                <label className="signIn__label"></label>
                                <input
                                    required 
                                    className='signIn__input'
                                    type="text" 
                                    placeholder="  " 
                                    value={login}
                                    onChange={e => {
                                        setLogin(e.target.value);
                                    }}
                                    onFocus={(e) => {if (e.target.placeholder === '  ') {e.target.placeholder = ''}}} 
                                    onBlur={(e) => {if (e.target.placeholder === '') {e.target.placeholder = '  '}}}
                                />
                            </FormField>
                            
                            <FormField className='signIn__field'>
                                <label className="signIn__label"></label>
                                <input  
                                    required 
                                    className='signIn__input'
                                    type="password" 
                                    value={password}
                                    onChange={e => {
                                        setPassword(e.target.value);
                                    }}
                                    placeholder=" " 
                                    onFocus={(e) => {if (e.target.placeholder === ' ') {e.target.placeholder = ''}}} 
                                    onBlur={(e) => {if (e.target.placeholder === '') {e.target.placeholder = ' '}}}
                                />
                            </FormField>

                            {/*  ,    */}
                            {!isDataCorrect && 
                                <p className='signIn__error'>
                                    <span className='signIn__invalid'>x</span>
                                       
                                </p>
                            }

                            <Button 
                                type="submit" 
                                variant='dark'
                                className="signIn__btn"
                            >
                                
                            </Button>
                                    
                        </Form.Group>
                    </Form>
                    <div className='signIn__question'>  ?   <Link to='/sign-up' className='signIn__link'></Link></div>
                </div>
            </div>

        </section>
    );
};

export default SignInPage;
